import { API_CONFIG } from 'config/core/api.config';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs/Rx";
import { Injectable } from '@angular/core';

import { AbstractService } from 'config/core/abstract-service';
import { TaxaEntregaDTO } from './../../../../models/domain/taxa-entrega.dto';

@Injectable()
export class TaxaEntregaService extends AbstractService {

    url:string = `${API_CONFIG.baseUrl}/taxaentrega`;

    constructor(public http: HttpClient) {
        super();
    }

    findAll(): Observable<TaxaEntregaDTO[]> {
        return this.http.get<TaxaEntregaDTO[]>(`${this.url}/all`, this.getHearderToken());
    }

    findAllAtivo(): Observable<TaxaEntregaDTO[]> {
        return this.http.get<TaxaEntregaDTO[]>(`${this.url}/`, this.getHearderToken());
    }

    findById(id: string): Observable<{}> {
        return this.http.get(`${this.url}/${id}`, this.getHearderToken());
    }

    salvar(info: TaxaEntregaDTO) {
        return this.http.post(`${this.url}/`, JSON.stringify(info), this.getHearderTokenNoResponse());
    }

    changeStatus(info: TaxaEntregaDTO) {
        return this.http.put(`${this.url}/${info.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
    }

    atualizar(info: TaxaEntregaDTO) {
        return this.http.put(`${this.url}/${info.id}`, JSON.stringify(info), this.getHearderTokenNoResponse());
    }

    excluir(info: TaxaEntregaDTO): Observable<{}> {
        let url = `${this.url}/${info.id}`;
        return this.http.delete(url, this.getHearderToken());
    }
}



