import { InfoNutricionalDTO } from './../../../../models/domain/info-nutricional.dto';
import { API_CONFIG } from 'config/core/api.config';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs/Rx";
import { Injectable } from '@angular/core';

import { AbstractService } from 'config/core/abstract-service';

@Injectable()
export class InfoNutricionalService extends AbstractService {

    url:string = `${API_CONFIG.baseUrl}/infonutricional`;

    constructor(public http: HttpClient) {
        super();
    }

    findAll(): Observable<InfoNutricionalDTO[]> {
        return this.http.get<InfoNutricionalDTO[]>(`${this.url}/all`, this.getHearderToken());
    }

    findAllAtivo(): Observable<InfoNutricionalDTO[]> {
        return this.http.get<InfoNutricionalDTO[]>(`${this.url}/`);
    }

    findById(id: string): Observable<{}> {
        return this.http.get(`${this.url}/${id}`, this.getHearderToken());
    }

    salvar(info: InfoNutricionalDTO) {
        return this.http.post(`${this.url}/`, JSON.stringify(info), this.getHearderTokenNoResponse());
    }

    changeStatus(info: InfoNutricionalDTO) {
        return this.http.put(`${this.url}/${info.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
    }

    atualizar(info: InfoNutricionalDTO) {
        return this.http.put(`${this.url}/${info.id}`, JSON.stringify(info), this.getHearderTokenNoResponse());
    }

    excluir(info: InfoNutricionalDTO): Observable<{}> {
        let url = `${this.url}/${info.id}`;
        return this.http.delete(url, this.getHearderToken());
    }
}



