import { ServicoEntregaDTO } from './../../../../models/domain/servico-entrega.dto';
import { API_CONFIG } from 'config/core/api.config';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs/Rx";
import { Injectable } from '@angular/core';

import { AbstractService } from 'config/core/abstract-service';

@Injectable()
export class ServicoEntregaService extends AbstractService {

    url:string = `${API_CONFIG.baseUrl}/servicoentrega`;

    constructor(public http: HttpClient) {
        super();
    }

    findAll(): Observable<ServicoEntregaDTO[]> {
        return this.http.get<ServicoEntregaDTO[]>(`${this.url}/all`, this.getHearderToken());
    }

    findAllAtivo(): Observable<ServicoEntregaDTO[]> {
        return this.http.get<ServicoEntregaDTO[]>(`${this.url}/`, this.getHearderToken());
    }

    findById(id: string): Observable<{}> {
        return this.http.get(`${this.url}/${id}`, this.getHearderToken());
    }

    salvar(info: ServicoEntregaDTO) {
        return this.http.post(`${this.url}/`, JSON.stringify(info), this.getHearderTokenNoResponse());
    }

    changeStatus(info: ServicoEntregaDTO) {
        return this.http.put(`${this.url}/${info.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
    }

    atualizar(info: ServicoEntregaDTO) {
        return this.http.put(`${this.url}/${info.id}`, JSON.stringify(info), this.getHearderTokenNoResponse());
    }

    excluir(info: ServicoEntregaDTO): Observable<{}> {
        let url = `${this.url}/${info.id}`;
        return this.http.delete(url, this.getHearderToken());
    }
}



