import { PerfilClienteComponent } from './perfil/perfil-cliente.component';
import { ConsultaCEPService } from './../../../../services/consulta-cep-service';
import { ClientePopupModalComponent } from './popup/cliente-popup-modal.component';
import { NgModule, LOCALE_ID } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { SelectModule } from 'ng2-select';
import { LaddaModule } from 'angular2-ladda';
import { ModalModule } from 'ng2-bootstrap/modal';

import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { SharedComponentModule } from './../../../shared/shared-component.module';

import { TextMaskModule } from 'angular2-text-mask';
import { TabsModule } from 'ng2-bootstrap/tabs';

import { ClienteService } from './cliente.service';
import { ClienteRoutingModule } from './cliente-routing.module';
import { StorageService } from './../../../../services/storage.service';
import { ListarClienteComponent } from './listar/listar-cliente.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    HttpClientModule,
    PopupModalModule,
    TabsModule,
    ClienteRoutingModule,
    LaddaModule,
    TextMaskModule
  ],
  declarations: [
    ListarClienteComponent,
    ClientePopupModalComponent,
    PerfilClienteComponent
  ],
  providers:[
    ClienteService,  
    StorageService,
    ConsultaCEPService
  ]
})
export class ClienteModule { }



