import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { TrataErrorService } from 'services/trata-error.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { FieldMessage } from './../../../../../models/config/field-message';
import { ClienteService } from './../cliente.service';
import { EstadoDTO } from './../../../../../models/domain/estado.dto';
import { ConsultaCEPService } from './../../../../../services/consulta-cep-service';
import { EnderecoDTO } from './../../../../../models/domain/endereco.dto';
import { API_CONFIG } from 'config/core/api.config';
import { ClienteDTO } from './../../../../../models/domain/cliente.dto';
import { CategoriaDTO } from 'models/domain/categoria.dto';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';



@Component({
  templateUrl: './perfil-cliente.component.html',
  styleUrls: ['./perfil-cliente.component.scss']
})
export class PerfilClienteComponent extends AbstractBaseComponent implements OnInit {

  enderecos: EnderecoDTO[] = [];

  constructor(
    public fb: FormBuilder,
    public clienteService: ClienteService,
    public consultaCEPService: ConsultaCEPService) {
    super();
  }

  ngOnInit() {
    this.criarForm();
    this.clienteService.infoClienteLogado().subscribe(
      res => this.alterarForm(res),
      error => TrataErrorService.tratarError(error)
    )
  }


  btnConfirmar(cliente: ClienteDTO) {
    cliente.enderecos = this.enderecos;
    cliente.urlImagem = this.urlFoto
    this.clienteService.atualizar(cliente).subscribe(
      res => EventEmitterService.emitirMensagemToasterSucesso('Perfil', 'Operação realizada com sucesso!!!'),
      error => TrataErrorService.tratarError(error))
  }

  mudarImage(evento) {
    this.atualizarFoto(evento.foto);
  }

  private alterarForm(cliente: ClienteDTO) {
    this.formulario.patchValue({
      id: cliente.id,
      nmPessoa: cliente.nmPessoa,
      nrCpf: cliente.nrCpf,
      dtNascimento: cliente.dtNascimento,
      status: cliente.status,
      urlImagem: cliente.urlImagem,
      emAlteracao: true,
      fone: {
        id: cliente.fone.id,
        nrFoneCelular: cliente.fone.nrFoneCelular,
        nrFoneResidencial: cliente.fone.nrFoneResidencial,
        nrFoneWhatsapp: cliente.fone.nrFoneWhatsapp
      }
    })
    if (cliente.enderecos != undefined) {
      this.enderecos = cliente.enderecos;
    }
    this.urlFoto = cliente.urlImagem == undefined ? `${API_CONFIG.bucketBaseUrl}/clientes/cli${cliente.id}.jpg` : cliente.urlImagem;
  }

  private criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      nmPessoa: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(120)]],
      nrCpf: ['', [Validators.required]],
      dtNascimento: [''],
      status: ['A'],
      urlImagem: [''],
      emAlteracao: [false],
      fone: this.fb.group({
        id: [''],
        nrFoneCelular: [''],
        nrFoneResidencial: [''],
        nrFoneWhatsapp: [''],
      })
    })
    this.enderecos = [];
    this.urlFoto = this.urlFotoAvatar;

  }

  private atualizarFoto(foto: any) {
    this.urlFoto = foto;
  }

  confirmarEndereco(ender: EnderecoDTO) {
    if (!ender.emAlteracao) {
      ender.id = '99' + (Math.floor((Math.random() * 100) + 1));
      this.enderecos.push(ender)
    } else {
      let position = this.enderecos.findIndex(x => x.id == ender.id);
      this.enderecos[position] = ender;
    }
  }


  excluirEndereco(ender: EnderecoDTO) {
    this.enderecos = this.enderecos.filter((end: EnderecoDTO) => end.id != ender.id)
  }
}


