import { TrataErrorService } from './../../../../../services/trata-error.service';
import { ClienteDTO } from './../../../../../models/domain/cliente.dto';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { API_CONFIG } from 'config/core/api.config';
import { ClienteService } from '../cliente.service';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { ProdutoDTO } from 'models/domain/produto.dto';

@Component({
  templateUrl: './listar-cliente.component.html',
  styleUrls: ['./listar-cliente.component.scss']
})
export class ListarClienteComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeStatusModal') popupChangeStatus;

  cliente: ClienteDTO;
  items: ClienteDTO[] = [];

  constructor(public clienteService: ClienteService, public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos()
  }

  selecionarItem(item: ClienteDTO) {
    this.cliente = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: ClienteDTO) {
    this.cliente = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.clienteService.changeStatus(this.cliente)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Cliente', 'Status do cliente foi alterado com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }

  excluir() {
    this.clienteService.excluir(this.cliente)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Cliente', 'Cliente foi excluído com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }

  private carregarTodos() {
    this.clienteService.findAll().subscribe(
      response => {this.items = response;},
      error => TrataErrorService.tratarError(error));
  }

}



